<template>
  <div class="about">
    <v-row>
      <!--        -->
      <v-row
          cols="24"
          md="10"
          sm="10">
        <v-card style="float: left"
                :loading="loading"
                class="mx-auto my-12"
                max-width="374"
        >
          <template slot="progress">
            <v-progress-linear
                color="deep-purple"
                height="10"
                indeterminate
            ></v-progress-linear>
          </template>

          <v-img
              height="250"
              style="opacity: 0.6"
              src="https://cdn.vuetifyjs.com/images/cards/cooking.png"
          ></v-img>

          <v-img class="v-btn--rounded" :src=this.avatar
                 style="margin-top: -10vh; float: left; width: 9vh; margin-left: 2vh;">
          </v-img>
          <v-row>
            <v-col>
              <v-card-title>{{ name }}</v-card-title>
            </v-col>
            <v-col cols="auto">
              <v-dialog
                  transition="dialog-bottom-transition"
                  max-width="600"
              >
                <template v-slot:activator="{ on, attrs }">
                  <v-btn
                      text
                      class="float-right green--text"
                      style="margin-top: 1vh"
                      v-bind="attrs"
                      v-on="on"
                  >
                  </v-btn>
                </template>
                <template v-slot:default="dialog">
                  <v-card>
                    <v-toolbar
                        color="primary"
                        dark
                    >
                    </v-toolbar>
                    <v-card-text>
                      <v-row style="margin-top: 2vh">
                        <v-col>
                          <v-text-field
                              label=""
                              v-model="name">
                          </v-text-field>
                        </v-col>
                        <v-col>
                          <v-text-field
                              label=""
                              v-model="province">
                          </v-text-field>
                        </v-col>

                      </v-row>

                      <v-col>
                        
                        <v-file-input
                            accept="image/*"
                            label=""
                            style="width: 70%"
                            v-model="file"
                        ></v-file-input>
                      </v-col>
                      <v-col>
                        <v-text-field
                            v-model="picName"
                            label="">
                        </v-text-field>
                      </v-col>
                    </v-card-text>

                    <v-card-actions class="justify-end">
                      <v-btn
                          text
                          @click="uploadPic">
                        
                      </v-btn>
                      <v-btn
                          text
                          @click="dialog.value = false"
                      >
                      </v-btn>

                      <v-btn
                          text
                          @click="dialog.value = false"
                      >
                      </v-btn>
                    </v-card-actions>
                  </v-card>
                </template>
              </v-dialog>
            </v-col>
          </v-row>

          <v-card-text>
            <v-row
                align="left"
                class="mx-0"
            >
            </v-row>

            <div class="my-4 text-subtitle-1">
              {{ province }}
            </div>

            <div>{{ signs }}</div>
          </v-card-text>

          <v-divider class="mx-4"></v-divider>

          <v-card-title>{{ this.score }}</v-card-title>

        </v-card>
      </v-row>

      <!--        -->
      <!--      <p style="margin-top: 5vh;">{{deadline}} </p>-->
      <v-row
          col="24"
          sm="10"
          md="6">
        <v-date-picker v-model="picker" width="40vh"
                       style="min-height: 45vh; margin-top: 5vh;margin-left: 4vh"></v-date-picker>
      </v-row>

      <!---->
      <v-row
          col="24"
          sm="8"
          md="8">
        <v-card
            class="mx-auto text-center"
            color="#64c887"
            dark
            width="55vh"
            height="35vh"
            style="float:right; margin-top: 5vh; margin-right: 10vh"
        >
          <v-card-text>
            <v-sheet color="rgba(0, 0, 0, .12)">
              <v-sparkline
                  :value="value"
                  color="rgba(255, 255, 255, .7)"
                  height="100"
                  padding="24"
                  stroke-linecap="round"
                  smooth
              >
                <template v-slot:label="item">
                  ${{ item.value }}
                </template>
              </v-sparkline>
            </v-sheet>
          </v-card-text>


          <v-card-title class="justify-center">
            <div>
              {{ province }} 
            </div>
          </v-card-title>

          <v-divider></v-divider>

          <v-card-actions class="justify-center">
            <v-btn
                block
                text
            >
              
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-row>
    </v-row>

    <!--      -->
    <v-col>
      <v-btn block color="success" align="left"></v-btn>
      <v-row align="right" style="margin-top: 2vh">
        <v-item-group
            v-model="window"
            class="shrink mr-6"
            mandatory
            tag="v-flex"
        >
          <v-item
              v-for="n in length"
              :key="n"
              v-slot="{ active, toggle }"
          >
            <div>
              <v-btn
                  :input-value="active"
                  icon
                  @click="toggle"
              >
                <v-icon>mdi-record</v-icon>
              </v-btn>
            </div>
          </v-item>
        </v-item-group>

        <v-col>
          <v-window
              v-model="window"
              class="elevation-1"
              vertical
          >
            <v-window-item
                v-for="n in length"
                :key="n"
            >
              <v-card flat>
                <v-card-text>
                  <v-row
                      class="mb-4"
                      align="center"
                  >
                    <v-avatar
                        color="blue"
                        class="mr-4"
                    ></v-avatar>
                    <strong class="text-h6"> {{ n }}</strong>
                    <v-spacer></v-spacer>
                    <v-btn icon>
                      <v-icon>mdi-account</v-icon>
                    </v-btn>
                  </v-row>
                  <p>
                    Peking
                    University21198521
                  </p>
                  <p>
                    Tsinghua
                    UniversityA9852112011111C9MIT
                  </p>
                  <p>
                    Beijing University of Posts and
                    Telecommunications211985CDIO1112011
                  </p>
                </v-card-text>
              </v-card>
            </v-window-item>
          </v-window>
        </v-col>
      </v-row>
    </v-col>
  </div>
</template>

<script>
import axios from "axios";

export default {

  data: () => ({
    loading: false,
    selection: 1,
    file:[],
    name: 'xiaoMing',
    email: '233@qq.com',
    score: '623',
    province: ' ',
    signs: '',
    picName: '',
    newPicAdd:'',
    avatar:'',

    //
    length: 3,
    window: 0,

    //
    value: [
      423,
      446,
      675,
      510,
      590,
      610,
      760,
      1000,
      0,
    ],

    //
    picker: (new Date(Date.now() - (new Date()).getTimezoneOffset() * 60000)).toISOString().substr(0, 10),
    deadline: '2',
  }),
  mounted() {
    this.avatar = localStorage.getItem("avatar");
    this.name = localStorage.getItem("name");
    this.score = localStorage.getItem("Score");
  },
  methods: {
    reserve() {
      this.loading = true
      setTimeout(() => (this.loading = false), 2000)
    },

    uploadPic: function () {
      var that = this;
      var url = 'http://127.0.0.1:9090/picture/savePic'

      const param = new FormData();

      // eslint-disable-next-line no-console
      param.append('file', that.file);
      param.append('pictureID', that.picName)
      const config = {
        headers: {'Content-Type': 'multipart/form-data'}
      };

      axios.post(url, param, config)
      .then(function (res){
        alert("" + res.data.msg);
      })

      that.getPic();
      location.reload();

    },

    getPic:function (){
      var that = this;
      var url = 'http://127.0.0.1:9090/picture/getPic'

      url = 'http://127.0.0.1:9090/picture/getPic'
      axios({
        url:url,
        methods: 'post',
        params:{
          pictureID:that.picName,
        }})
          .then(function (response){
            that.newPicAdd = "http://127.0.0.1:9090/" + response.data;
            localStorage.setItem("avatar",that.newPicAdd);
            that.picName = that.newPicAdd;
            location.reload();
          })

    }

  }
}
</script>
